import React,{Component} from 'react';
import { Card, WingBlank, WhiteSpace ,Flex ,SearchBar,Button,InputItem} from 'antd-mobile';

import cy from '../json/cy.js';


import chinastarlogistics from '../img/chinastarlogistics.png';


export default class Getkuadi extends Component {
    constructor(props) {
        super(props);
        this.state = {
          disabled: false,
          str :'请输入一个成语或者要查询的字',
          list:[],
        };
      }
     // 组件挂载之前
    componentWillMount() {
      console.log(cy['叶']);
      
    }

    // 组件挂载后
    componentDidMount() {
    }

      render() {
        const cylist  = cy;
        const forcard = this.state.list.map((item) =>
        <Flex>
          <Flex.Item style={{textAlign:'center',marginTop:10}}>
              <a onClick={e=>{
                let jieguo = cylist[item.slice(-1)];
                if(jieguo!=undefined){
                  this.setState({
                    list:jieguo,
                    str:item,
                  });
                }else{
                  this.setState({
                    list:[],
                    str:item,
                  });
                }
              }}>{item}</a>
          </Flex.Item>
        </Flex>
          );
        return (
          <div>
            <WingBlank size="lg" style={{marginTop:20}}>
            <Card>
            <Flex>
              <Flex.Item>
                  <InputItem
                  placeholder="点击输入"
                  onBlur={(v) => {
                    let jieguo = cylist[v.slice(-1)];
                    if(jieguo!=undefined){
                      this.setState({
                        list:jieguo,
                        str:v,
                      });
                    }else{
                      this.setState({
                        list:[],
                        str:v,
                      });
                    }
                  }}
                >输入成语</InputItem>
              </Flex.Item>
              </Flex><Flex>
              <Flex.Item>
              <WingBlank size="lg" style={{marginTop:20}}>
                <p  style={{fontSize:13,marginTop:20,color:'#888'}}>当前成语：{this.state.str}</p>
                </WingBlank>
              </Flex.Item>
            </Flex>
              
            </Card>
            <Card>
            <WingBlank size="lg" style={{marginTop:20}}>
            {forcard}
            </WingBlank>
          </Card>
          </WingBlank>
          </div>
        )}
}